"use client";

import { useState } from "react";
import { DashboardWrapper } from "@/components/layout/dashboard-wrapper";
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import { Badge } from "@/components/ui/badge";
import { ScheduleSessionModal } from "@/components/scheduling/schedule-session-modal";
import { CalendarPlus, Star, BookOpen, ArrowLeft } from "lucide-react";
import { useParams, useRouter } from "next/navigation";

// Mock Tutor Data Interface (can be shared or imported)
interface Tutor {
  id: string;
  name: string;
  avatar: string;
  subjects: string[];
  bio: string;
  rating: number;
  // Add more details if needed
  experience?: string;
  education?: string;
  reviews?: { student: string; rating: number; comment: string }[];
}

// Mock Data (same as list page, potentially fetch details here)
const mockTutors: Tutor[] = [
  {
    id: "tutor1",
    name: "Dr. Emily Carter",
    avatar: "/avatars/emily-carter.png",
    subjects: ["Calculus", "Linear Algebra"],
    bio: "Passionate about making math understandable and engaging. Proven track record of helping students improve their grades and confidence.",
    rating: 4.9,
    experience: "5+ years tutoring experience, former university TA.",
    education: "PhD in Mathematics",
    reviews: [
        { student: "Jane S.", rating: 5, comment: "Dr. Carter explains things so clearly!" },
        { student: "Bob K.", rating: 5, comment: "Helped me ace my calculus final." },
    ]
  },
  {
    id: "tutor2",
    name: "Dr. Alan Grant",
    avatar: "/avatars/alan-grant.png",
    subjects: ["Physics", "Astrophysics"],
    bio: "Experienced physicist with a knack for explaining complex concepts using real-world examples.",
    rating: 4.8,
    experience: "10 years research, 7 years tutoring.",
    education: "PhD in Physics",
    reviews: [
        { student: "Alice B.", rating: 5, comment: "Made physics fun!" },
    ]
  },
  {
    id: "tutor3",
    name: "Prof. Sarah Harding",
    avatar: "/avatars/sarah-harding.png",
    subjects: ["Chemistry", "Organic Chemistry"],
    bio: "Dedicated to helping students succeed in chemistry through practice and clear explanations.",
    rating: 4.7,
    experience: "Professor of Chemistry for 8 years.",
    education: "MSc in Chemistry",
     reviews: []
  },
];

export default function TutorProfilePage() {
  const params = useParams();
  const router = useRouter();
  const tutorId = params.tutorId as string;

  const [isScheduleModalOpen, setIsScheduleModalOpen] = useState(false);

  // Find the tutor (replace with actual data fetching)
  const tutor = mockTutors.find(t => t.id === tutorId);

  if (!tutor) {
    return (
      <DashboardWrapper>
        <div className="container mx-auto py-8">
            <Button variant="outline" onClick={() => router.back()} className="mb-4">
               <ArrowLeft className="mr-2 h-4 w-4" /> Back to Tutors
            </Button>
           <p>Tutor not found.</p>
        </div>
      </DashboardWrapper>
    );
  }

  const handleScheduleClick = () => {
    setIsScheduleModalOpen(true);
  };

  return (
    <DashboardWrapper>
      <div className="container mx-auto py-8 space-y-6">
         {/* Back Button & Header */}
         <div>
             <Button variant="outline" size="sm" onClick={() => router.back()} className="mb-4">
               <ArrowLeft className="mr-2 h-4 w-4" /> Back to Tutors
            </Button>
            <div className="flex flex-col sm:flex-row items-center gap-6">
               <Avatar className="h-24 w-24 border text-4xl">
                  <AvatarImage src={tutor.avatar} alt={tutor.name} />
                  <AvatarFallback>{tutor.name.split(' ').map(n => n[0]).join('')}</AvatarFallback>
               </Avatar>
               <div className="flex-1 text-center sm:text-left">
                  <h1 className="text-3xl font-bold mb-1">{tutor.name}</h1>
                  <div className="flex items-center justify-center sm:justify-start gap-1 text-muted-foreground mb-2">
                     <Star className="h-4 w-4 text-primary" />
                     <span className="font-medium text-primary">{tutor.rating}</span>
                     <span>({tutor.reviews?.length || 0} reviews)</span>
                  </div>
                   <div className="flex flex-wrap gap-1 justify-center sm:justify-start mb-3">
                      {tutor.subjects.map((subject) => (
                         <Badge key={subject} variant="secondary">{subject}</Badge>
                      ))}
                   </div>
                   <Button onClick={handleScheduleClick}>
                       <CalendarPlus className="mr-2 h-4 w-4" /> Schedule Session
                   </Button>
               </div>
            </div>
         </div>

         {/* Main Content Grid */}
         <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
            {/* Left Column: About, Experience, Education */}
            <div className="lg:col-span-2 space-y-6">
                <Card>
                   <CardHeader>
                      <CardTitle>About Me</CardTitle>
                   </CardHeader>
                   <CardContent>
                      <p className="text-muted-foreground text-sm">{tutor.bio}</p>
                   </CardContent>
                </Card>
                {tutor.experience && (
                    <Card>
                       <CardHeader>
                          <CardTitle>Experience</CardTitle>
                       </CardHeader>
                       <CardContent>
                          <p className="text-muted-foreground text-sm">{tutor.experience}</p>
                       </CardContent>
                    </Card>
                )}
                 {tutor.education && (
                    <Card>
                       <CardHeader>
                          <CardTitle>Education</CardTitle>
                       </CardHeader>
                       <CardContent>
                          <p className="text-muted-foreground text-sm">{tutor.education}</p>
                       </CardContent>
                    </Card>
                )}
            </div>

            {/* Right Column: Reviews */}
            <div className="lg:col-span-1">
               <Card>
                  <CardHeader>
                     <CardTitle>Reviews</CardTitle>
                     <CardDescription>{tutor.reviews?.length || 0} reviews</CardDescription>
                  </CardHeader>
                  <CardContent className="space-y-4">
                     {tutor.reviews && tutor.reviews.length > 0 ? (
                        tutor.reviews.slice(0, 5).map((review, index) => ( // Show first 5 reviews
                           <div key={index} className="border-b pb-3 last:border-b-0 last:pb-0">
                              <div className="flex items-center justify-between mb-1">
                                 <span className="font-medium text-sm">{review.student}</span>
                                  <div className="flex items-center gap-0.5 text-primary">
                                    {[...Array(5)].map((_, i) => (
                                       <Star key={i} className={`h-3 w-3 ${i < review.rating ? 'fill-current' : 'fill-muted stroke-muted-foreground'}`} />
                                    ))}
                                 </div>
                              </div>
                              <p className="text-sm text-muted-foreground">{review.comment}</p>
                           </div>
                        ))
                     ) : (
                        <p className="text-sm text-muted-foreground">No reviews yet.</p>
                     )}
                     {/* Optional: Add link to view all reviews */}                  </CardContent>
               </Card>
            </div>
         </div>
      </div>

      {/* Scheduling Modal */}
      <ScheduleSessionModal
        isOpen={isScheduleModalOpen}
        onClose={() => setIsScheduleModalOpen(false)}
        role="STUDENT"
        availableUsers={tutor ? [tutor] : []} // Pass only the current tutor
      />
    </DashboardWrapper>
  );
} 